<!-- -->
<template>
<div class='wrapper'>
    <v-head></v-head>
  <div class="clear"></div>
 <div class="el-main">
     <div id="infoform">
<el-form id="form1" ref="form1" :inline="true" :model="form1" :rules="rule1" label-width="100px" >
     <el-form-item label="账号：" prop="professor_id">
        <el-input v-model="form1.professor_id" :disabled="true" style="width:220px" ></el-input>
     </el-form-item>
    <el-form-item label="姓名：" prop="pname">
        <el-input v-model="form1.pname" style="width:220px" ></el-input>
     </el-form-item>
     <el-form-item label="性别：">
       <el-select v-model="form1.psex"   placeholder="请选择性别" style="width:220px">
      <el-option label=" " value="0"></el-option>
      <el-option label="男" value="1"></el-option>
      <el-option label="女" value="2"></el-option>
    </el-select>
     </el-form-item>
    <el-form-item label="学院：" prop="pdepartment">
        <el-input v-model="form1.pdepartment"  style="width:220px"></el-input>
     </el-form-item>
    <el-form-item label="教龄：" prop="pteaching_age">
        <el-input v-model="form1.pteaching_age" type="number"  max=999 style="width:220px"></el-input>
     </el-form-item>
    <el-form-item label="职务：" prop="ptitle" >
       <el-select v-model="form1.ptitle"  placeholder="请选择职务" style="width:220px">
      <el-option label="  " value="null"></el-option>
      <el-option label="助教" value="assistant"></el-option>
      <el-option label="讲师" value="lecturer"></el-option>
       <el-option label="副教授" value="adjpro"></el-option>
        <el-option label="教授" value="professor"></el-option>
    </el-select>
         </el-form-item>
     <el-form-item label="教育背景：" prop="peducational_background">
        <el-input v-model="form1.peducational_background" style="width:220px" type="text" ></el-input>
      <el-form-item label="出生日期：" >
        <el-date-picker v-model="form1.pbirthday" id="birday" @change="getBirthTime" format="yyyy-MM-dd"  type="date" style="width:220px" placeholder="选择日期" :picker-options="pickerOptions"></el-date-picker>
     </el-form-item>
         <el-form-item label="民族：" prop="pnationality">
        <el-input v-model="form1.pnationality" style="width:220px"  type="text"></el-input>
     </el-form-item>
         <el-form-item label="政治面貌：" prop="ppolitics_status">
        <el-input v-model="form1.ppolitics_status"  type="text" style="width:220px" ></el-input>
     </el-form-item>
         <el-form-item label="方向：" prop="parea_of_interest">
        <el-input v-model="form1.parea_of_interest"  type="text" style="width:220px" ></el-input>
     </el-form-item>
	 </el-form-item>
         <el-form-item label="联系方式：" prop="pcontact">
        <el-input v-model="form1.pcontact"  type="text" style="width:220px" ></el-input>
     </el-form-item>
	 <el-col></el-col>
</el-form>
<el-form id="form2" :inline="true" label-width="100px" >
<el-form-item>
       <el-button type="primary" @click="modifyForm('form1')">保存</el-button>
                 <el-button type="success" @click="resetForm('form1')">重置</el-button>
                 <el-button @click="exit('form1')">返回</el-button>
     </el-form-item>
   
     </el-form>
        </div>
      </div>
    </div>
 
</template>
    
<script>
  import vHead from '../commons/AdminstratorNav.vue';
  export default {
     created(){
      
       this.form1.professor_id=localStorage.getItem("selectRow");
       var self=this;
        $.ajax({
          type:'post',
          dataType:'json',
          url:'/teacher/fileInfo',
          data:{
            "id":this.form1.professor_id
          },
          success:function(response){
            console.log("过来了");
                var array =  response.stringBirthday.split("-");
                var birday = new Date(array[0], array[1], array[2]);
               console.log(birday);
                self.form1.pteaching_age=response.teachingAge;
                self.form1.pname=response.name;
                self.form1.pdepartment=response.depName;
                self.form1.peducational_background=response.educationBackground;       //
                self.form1.pnationality=response.nationality;
                self.form1.ppolitics_status=response.politicalStatus;
                self.form1.parea_of_interest=response.areaInterest;
                self.form1.ptitle=response.title;
                self.form1.psex=self.getSex(response.sex);
                self.form1.pcontact=response.contactInfomation;
                self.form1.pbirthday=birday
                

                console.log(self.form1);
          },error:function(err){
            console.log(err);
            console.log("error");
          }
        })

     },
     components:{
      vHead,
     },
      data: function(){
      return {
          hostUrl:'/',
          pickerOptions: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          }
        },
          form1:{
              professor_id: '',
              pname: '',
              pdepartment:'',
              pteaching_age:'',
              peducational_background:'',
              pbirthday: '',
              pnationality: '',
              pcontact_information: '',
              ppolitics_status: '',
              parea_of_interest: '',
              psex:'',
              ptitle:'',
			  pcontact:''
          },
          rule1:{
              professor_id: [
            { required: true, message: '请输入学号', trigger: 'blur' },
            { min: 12, max: 12, message: '长度为12个字符', trigger: 'blur' }
          ],
          pname: [
            { required: true, message: '输入名字', trigger: 'blur' },
          ],
          pdepartment: [
              { required: true, message: '请输入学生学院', trigger: 'blur' }
          ],
		   pteaching_age: [
            { required: true, message: '请输入教龄', trigger: 'blur' },
          ],
		   peducational_background: [
            { required: true, message: '请输入教育背景', trigger: 'blur' },
          ],
		   pbirthday:[
             { type: 'date', required: true, message: '请选择时间', trigger: 'change' },
         ],
		  pnationality:[
             { required: true, message: '请输入民族', trigger: 'change' },
         ],
		   ppolitics_status:[
             { required: true, message: '请输入政治面貌', trigger: 'change' },
         ],
		  parea_of_interest:[
             { required: true, message: '请输入方向', trigger: 'change' },
         ],
		 ptitle:[
             { required: true, message: '请选择职务', trigger: 'change' },
         ],
         psex:[
             { required: true, message: '请选择性别', trigger: 'change' },
         ],
         pcontact:[
             { required: true, message: '请填写联系方式', trigger: 'change' },
         ]

          }
      }
    },
        methods: {
            getSex(sex){
              if(sex==0)
              return "0"
              else if(sex==1)
              return "1"
              else return "2"
          },
            getBirthTime(date){
              this.form1.pbirthday=date;
               console.log(typeof this.form1.pbirthday);
          },
         
       modifyForm(formName) {
        console.log("11");
        var vm=this;
        const h = this.$createElement;
        this.$msgbox({
          title: '消息',
          message: h('p', null, [
            h('span', null, '是否确定要'),
            h('i', { style: 'color: teal' }, '修改')
          ]),
          showCancelButton: true,
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          beforeClose: (action, instance, done) => {
            if (action === 'confirm') {
              console.log("?");
              $.ajax({
                type:'post',
                url:'/teacher/setInfo',
                dataType:'json',
                // async:false,
                data:{
                  "id":this.form1.professor_id,
                  "teachingAge":this.form1.pteaching_age,
                  "nationality":this.form1.pnationality,
                  "educationBackground":this.form1.peducational_background,
                  "politicalStatus":this.form1.ppolitics_status,
                  "areaInterest":this.form1.parea_of_interest,
                  "sex":this.form1.psex,
                  "title":this.form1.ptitle,
                  "contact":this.form1.pcontact,
                  "birthday":this.form1.pbirthday
                },
                success:function(response){
                    
                    if(response.data===1){
                       vm.$message({
                            type: 'success',
                            message: '修改成功!'
                       })
                    }else{
                       vm.$message({
                            type: 'info',
                            message: '网络错误!'
                       })
                    }
                },
                error:function(err){
                    console.log(err)
                }
            })
            done();
            } else {
              console.log("11111111111111111");
               vm.$message({
                        type: 'info',
                        message: '已取消修改'
                      });    
              done();
            }
          }
        })
      },
  
    
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      exit(formName) {
         localStorage.setItem("firsts","1");
         console.log(localStorage)
          this.$router.push('/adminstrator/teacherInfo')
      }
      }
    
  }
</script>
<style scoped>
.clear{
  clear: both;
}
.el-main{
    margin-top:3px;
    background-color:#eef1f6;
    color: #333;
    text-align: center;
    height:800px;
}
#infoform{
    padding-top: 60px;
}
#form1{
    width:80%;
    margin:0px auto;
}
#form2{
    width:220px;
    margin:0px auto;
    float: center;
}
  .el-form-item{
      float:left;
  }
</style>


